.sidebar {
  width:300px !important;
  height: 600px;
  border-radius: 5px;
  box-shadow: 0px 4px 8px rgba(56, 54, 54);
  overflow-y: scroll;
}

.planet {
  width: 272px ;
  height: 200px;
  background: #FFFFFF;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
}
.nav {
  width: 200px;
}
.text-dark {
  color: #121C33 !important;
}

.character  {
  width: 16rem !important;
  height: 200px;
  background: #FFFFFF;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 16px !important;
  cursor:pointer
}
.char-img {
  width: 100% !important;
  height: 120px;
  border-top-left-radius: 16px !important;
  border-top-right-radius: 16px !important;

}

.planetForm {
  font-family: Poppins;
  font-weight: 900;
  font-size: 48px;
  line-height: 72px;
  color: #121C33;
  letter-spacing: 1px;
}
.formimg {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}
.closeForm {
  width: 112px !important;
  height: 48px !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  line-height: 24px !important;  
  background: rgba(18, 28, 51, 0.05) !important;
  color: #121C33 !important;
  border-radius: 8px !important;
  border: none !important;
}
.createForm {
  width: 171px !important;
  height: 48px !important;
  right: 0px !important;
  bottom: 0px !important;
  background: #121C33 !important;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2) !important;
  border-radius: 8px !important;
  border: none !important;
}
.formInput {
  background: #F5F5F5 !important;
}
.pasteimg {
  font-style: normal !important;
  font-weight: normal !important;
  font-size: 16px !important;
  line-height: 24px !important;
  color: #121C33 !important;
  opacity: 0.6;

}
.right{
  position:fixed;
  top:5rem;
  right:1rem;
  padding:10px;
  border-radius: 30px;
}
.char-right{
  border-radius: 30px;
}

@media only screen and (max-width: 320px){
	.char-img {
    width: 250px;
  }  
}

